@font-face {
  font-family: alrightSans;
  src: url(./alright.otf);
}

.name {
  font-size: 90px;
  font-weight: 100;
  margin: 0;
}

.container {
  position: relative;
  width: 1180px;
  margin: 200px auto;
}

.bio {
  display: inline-block;
}

.intro {
  margin: 0;
  text-align: left;
}

.app {
  font-family: alrightSans;
  margin: 0;
}

.project-container {
  margin-top: 300px;
}

.project-container-title {
  display: inline;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 40px;
  margin: 0;
  border-bottom-style: dotted;
  border-bottom-color: #3F51B5;
}

.project-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.project-list::after {
  content: " ";
  display: block; 
  height: 0; 
  clear: both;
}

.project-list-item {
  width: 45%;
  float: left;
  margin: 5% 5% 0 0;
}




